@import url('https://cdn.jsdelivr.net/npm/tailwindcss@2.2.19/dist/tailwind.min.css');

/* Sidebar styles */
.sidebar {
  width: 200px;
  background: #1a1a2e;
  color: white;
  padding: 60px 20px 20px;
}

.sidebar h2 {
  margin: 0;
  color: #00d4ff;
}

.sidebar ul {
  list-style: none;
  padding: 0;
}

.sidebar ul li {
  padding: 10px;
  color: #00d4ff;
  cursor: pointer;
  border-radius: 0.5rem;
}

.sidebar ul li:hover {
  background: #16213e;
}

/* Sidebar button base (gradient embossed) */
.sidebar-button {
  position: relative;
  display: block;
  padding: 10px 20px;
  color: white;
  text-align: center;
  border-radius: 4px;
  background: linear-gradient(145deg, #2b2e3c, #1c1f2a);
  box-shadow:
    inset 3px 3px 7px rgba(255, 255, 255, 0.1),
    inset -3px -3px 7px rgba(0, 0, 0, 0.7),
    6px 6px 12px rgba(0, 0, 0, 0.9);
  transition: all 0.3s ease;
  cursor: pointer;
  user-select: none;
  font-weight: 600;
  font-size: 1rem;
  border: 2px solid transparent;
}

/* Shine wave effect on hover */
.sidebar-button::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  height: 100%;
  width: 100%;
  background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.3), transparent);
  transition: left 0.3s ease;
  z-index: 0;
}

.sidebar-button:hover::before {
  left: 0;
}

.sidebar-button:hover {
  background: linear-gradient(145deg, #1c1f2a, #2b2e3c);
  box-shadow:
    inset 2px 2px 6px rgba(255, 255, 255, 0.15),
    inset -2px -2px 6px rgba(0, 0, 0, 0.9),
    8px 8px 16px rgba(0, 0, 0, 1);
  transform: translateY(-4px) scale(1.05);
  z-index: 2;
}

