/* Neon glow using currentColor */
.neon-glow {
  box-shadow:
    0 0 6px currentColor,
    0 0 12px currentColor,
    0 0 18px currentColor,
    inset 0 0 6px currentColor;
  transition: box-shadow 0.3s ease;
}

.neon-glow:hover {
  box-shadow:
    0 0 10px currentColor,
    0 0 20px currentColor,
    0 0 30px currentColor,
    inset 0 0 10px currentColor;
}

/* Title and wave effect */
.title-wave {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(45deg, rgba(34, 211, 238, 0.5), rgba(34, 211, 238, 0.2), transparent);
  background-size: 200% 100%;
  z-index: -1;
  border-radius: 0.75rem;
}

/* Shimmer wrapper for title bar */
.shimmer-wrapper {
  position: relative;
  overflow: hidden;
  background-image: linear-gradient(to right, #1e293b, #334155, #1e293b);
  border: 2px solid #00ffff;
  border-radius: 0.75rem;
  box-shadow: inset 4px 4px 8px rgba(255,255,255,0.08),
              inset -4px -4px 8px rgba(0,0,0,0.45),
              0 0 25px rgba(0,255,255,0.35);
}

/* Shimmer bar animation */
.shimmer-wrapper::before {
  content: '';
  position: absolute;
  top: 0;
  left: -150%;
  width: 150%;
  height: 100%;
  background: linear-gradient(120deg, transparent, rgba(255,255,255,0.25), transparent);
  animation: shimmer-bar 2s infinite linear;
  pointer-events: none;
}

@keyframes shimmer-bar {
  0% { transform: translateX(-100%); }
  100% { transform: translateX(100%); }
}

/* Grid layout adjustments */
.space-y-3 > div {
  display: contents;
}

.grid-cols-4 { grid-template-columns: repeat(4, 1fr); }
.grid-cols-3 { grid-template-columns: repeat(3, 1fr); }
.grid-cols-2 { grid-template-columns: repeat(2, 1fr); }
.grid-cols-1 { grid-template-columns: 1fr; }

.gap-3 { gap: 12px; }
.gap-6 { gap: 1.5rem; }

/* Text styles */
.text-white-300 { color: #d1d5db; }
.text-white-200 { color: #e5e7eb; }
.text-white-400 { color: #9ca3af; }
.text-gray-400 { color: #9ca3af; }
.text-xs { font-size: 0.75rem; line-height: 1rem; }
.text-sm { font-size: 0.875rem; line-height: 1.25rem; }
.text-lg { font-size: 1.125rem; line-height: 1.75rem; }
.text-2xl { font-size: 1.5rem; line-height: 2rem; font-weight: 700; }
.font-semibold { font-weight: 600; }
.uppercase { text-transform: uppercase; }
.tracking-wide { letter-spacing: 0.025em; }
.mb-2 { margin-bottom: 0.5rem; }
.mb-4 { margin-bottom: 1rem; }
.mt-2 { margin-top: 0.5rem; }
.drop-shadow { filter: drop-shadow(0 1px 2px rgb(0 0 0 / 0.1)) drop-shadow(0 1px 1px rgb(0 0 0 / 0.06)); }